import { OrbitControls, Environment ,useGLTF } from "@react-three/drei"
import { Perf } from "r3f-perf"

export default function App() {
    const model = useGLTF('./cupra_formentor.glb')
    return <>
        <Perf position="top-left" />
        <Environment files={'./shanghai_bund_2k.hdr'} ground={{height: 7.5, radius: 150, scale: 100,}} />
        <OrbitControls makeDefault maxPolarAngle={Math.PI/2.2} />    
        <primitive object={model.scene} position-y={0} scale={3} />
    </>
}